@import "../../scss/mixins.scss";

.recipes {
  @include wrapper;
  min-height: 100vh;
  padding-top: 11vh;
  margin-bottom: 2vh;

  .recipeTitle {
    @include titleContainer;
  }

  .recipeList {
    display: grid;
    grid-template-columns: repeat(1, 100%);
    gap: 1%;
  }

  .noRecipe {
    width: 250px;
    margin: 110px auto;
    padding: 20px;
    text-align: center;
    border: solid 1px #eee;
    box-shadow: 0 3px 4px #ccc;
    border-radius: 3px;
    color: grey;
  }

  .pageButtons {
    padding-top: 5rem;
    text-align: center;
  }

  .pagination {
    background-color: $color--two;
    border: none;
    border-radius: 0.3rem;
    color: white;
    margin: 0 0.5rem;
    padding: 0.6rem 1.2rem;
    outline: none;
  }
}

@media screen and (min-width: 780px) {
  .recipes {
    .recipeList {
      grid-template-columns: repeat(2, 48%);
      gap: 4% 2%;
    }
  }
}

@media screen and (min-width: 1125px) {
  .recipes {
    .recipeList {
      grid-template-columns: repeat(3, 32%);
      gap: 4% 2%;
    }
  }
}
