@import "../../scss/variables.scss";
@import "../../scss/mixins.scss";

.recipeContainer {
  &:hover {
    box-shadow: 0 0 12px 0 #ccc;
  }

  .recipe {
    width: 100%;
    position: relative;
    box-shadow: 0 0 0.3rem 0 $color--six;
    border-radius: 2px;
  }

  h3 {
    color: $color--two;
    padding-bottom: 0.2rem;
  }

  .imageContainer {
    width: 100%;
    height: 150px;
    background-position: center;
    background-size: cover;
    overflow: hidden;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .recipe__details {
    padding: 0.8rem;
  }

  .recipe__info {
    @include flexbox(space-between, center);
  }

  .info {
    color: $color--six;
  }

  .more__info {
    font-style: italic;
    text-decoration: underline;
    color: #0099ff;
  }
}
